import {Page} from  "../dataStructure/page";
import {CustomTitle} from '../components/custom';
import { Button, HStack, Container, Box } from "@chakra-ui/react";
import NewTask from "../components/newTask";
import {DeleteIcon} from "@chakra-ui/icons";
import firebase from '../firebase';
import {useEffect, useState} from 'react';

interface dashboard {
    title:string,
    onChange: any,
}
const Dashboard:React.FC<dashboard> = ({title, onChange}) => {
    
  //localStorage.clear();
    const email = localStorage.getItem('email');
    const ref = firebase.firestore().collection('users').doc('users_document').collection(email as string).doc('data');
    //console.log(ref);
    
  interface LooseObject {
      [key: string]: any
  }

    const newUser = {
      fName: '',
      lName: '',
      pssw: '',
      lists: {} as LooseObject,
      list: '',
    }

    
    const [dataFireStore, setDataFireStore ] = useState<typeof newUser>(newUser);

    const effect = () => {
      ref.onSnapshot((querySnapshot) => {
        setDataFireStore(querySnapshot.data()? querySnapshot.data() as typeof newUser: newUser);
        })
    }
  


    


    const [newItemField, setNewItemField] = useState(<></>);     
    const [newItem, setNewItem] = useState(''); 
    const [addText, setAddText] = useState('add');     
    const [allItems, setAllItems] = useState<string[]>([]);   

  useEffect(() => {

      effect(); 
                                          
    }, []);

    useEffect(() => {
      setAllItems(Object.keys(dataFireStore.lists));  
                                              
    }, [dataFireStore]);

    

    return (
        <>    
            <CustomTitle title={title}/>

            <Box id='box' mb={'10px'}>
            <HStack spacing={1}> 
                {newItemField}
                <Button  visibility={newItem === ''? 'collapse': 'visible'} colorScheme="blue" onClick={ () => {
                                        setAddText('<<');
                                        

                                        if(!(newItem in dataFireStore.lists)){

                                          const data = dataFireStore;
                                          data.list = newItem;
                                          
                                          data.lists[newItem] =  [];
                                          setDataFireStore(data);
                                          ref.set(
                                            data
                                          ).then(() => {
                                            console.log('DONE DATA');
                                          });
                                          
                                          
                                          
                                          setTimeout(() => onChange(Page.List), 500);
                                      }
                                        

                                        
                                        

                                        setNewItem('');
                                        setTimeout(() => setNewItemField(<></>), 200);
                                    }}>
                                    {addText}
                                    </Button>
            </HStack>
            </Box>            

            <Container centerContent={true}>
            <HStack spacing={8}>
                      
            




            <Container centerContent={true}>
            <HStack spacing={8}>
                <Button  colorScheme="blue" onClick={() => {
                    setNewItemField(
                                    
                                    <NewTask onChange={setNewItem} type={'new list'}/>
                                                                      
                                );
                                setAddText('add');
                                setTimeout(() =>
                                (document.getElementById("new list") as HTMLElement).focus(), 100); 
                }}>
                    New List
                </Button>
             
                                        
            </HStack>
            </Container>

            </HStack>
            </Container>

            <Box id='box' m={'20px'} style={{width: '80%', backgroundColor: '#dcecf7', borderRadius:'20px' }}
            onChange={() => {
                
            }}>
                {allItems.map((item, i) => 
                    <div  key={i} style={{color: "#3182ce", fontSize:"20px", margin: '10px'}} >
                        
                        <HStack spacing={5}>
                        <DeleteIcon color="gray.300" onClick={() => {

                            const data = dataFireStore;
                            delete data.lists[item];
                            setDataFireStore(data);
                            ref.set(
                              data
                            ).then(() => {
                              console.log('DONE DATA');
                            });
                            
                             const aux1 = [...allItems.slice(0,i)];
                             const aux2 = [...allItems.slice(i+1,allItems.length)];
                             setAllItems(aux1.concat(aux2));

                             
                        }}/>
                        
                        <Box onClick={() => {
                            

                            const data = dataFireStore;
                            data.list = item;
                            setDataFireStore(data);
                            ref.set(
                              data
                            ).then(() => {
                              console.log('DONE DATA');
                            });

                            
                            setTimeout(() => onChange(Page.List), 50);
                        }}
                        >{`${item}`}</Box>
                            
                        </HStack>
                        
                        
                    </div>
                )}
            </Box>








        </>
    );
}

export default Dashboard;