import {Formik, 
    Form}  from 'formik';
import {Button, 
    Container, 
    } from "@chakra-ui/react";
import * as Yup from 'yup';
import '../style/field.scss';
import {Page} from  "../dataStructure/page";
import {CustomTextInput, CustomTitle} from '../components/custom';
import {useState} from 'react';



interface signIn {
changePage:(page: Page) => void,
}


const  SignIn: React.FC<signIn> = ({changePage}) => {

  const [state, setstate] = useState<string>('');  
  const [isEmailValid, setIsEmailValid] = useState<boolean>(false);


  const setState = (email:string) => {
    const data = localStorage.getItem(email);
    
    setstate(data?JSON.parse(data).pssw:'');
    setIsEmailValid(data?true:false);
  }

  const emailFilter = (key:string)=> {return (key !== 'email');}
  const emails = Object.keys(localStorage).filter(emailFilter);
  return (
    <>
    <Formik 
    initialValues={{
        email:'',
        password:'',
    }}
    validationSchema={Yup.object({
        email: Yup.string()
            .email('Invalid email address.')
            .required('Please your email address.')
            .oneOf(emails,'Sorry, this email is not linked to any account.'),
        password: Yup.string()
        .when('email', (email:boolean, field: any) =>
          email && isEmailValid ? field.required('Please enter your password.').oneOf([state], 'You must use the same password you inserted before.') : field
            ),

    })}
    onSubmit={(values, {setSubmitting, resetForm}) => {
        setTimeout(()=> {
            
            localStorage.setItem('email', values.email);
            changePage(Page.DashBoard);

            console.log(values);
            console.log(typeof values);
            resetForm();
            setSubmitting(false);
        }, 1500);
    }}
    >

        {props => (

            <Form>
                <Container centerContent={true}>
                <CustomTitle title='Sign In'/>                  
                <CustomTextInput label='E-mail' name='email' type='text' placeholder='example@email.com' func={setState} handleBlur={props.handleBlur}/>                 
                <CustomTextInput label='Password' name='password' type='password' placeholder='********' handleBlur={props.handleBlur}/>
                <Button type='submit' colorScheme='blue' className='button'> {props.isSubmitting? 'Loading...':'Sign In'}</Button>
                </Container>
            </Form>
        )}


    </Formik>            
    </>
)
}

export default SignIn
